import { AbstractControl, ValidationErrors, ValidatorFn } from '@angular/forms';
import { isNullOrUndefined } from '@sofico-framework/utils';

export function chipsRegexValidator(regex: RegExp): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    if (
      isNullOrUndefined(regex) ||
      isNullOrUndefined(control?.value) ||
      !Array.isArray(control.value)
    ) {
      return null;
    }

    if (control.value?.some(chip => !chip.match(regex))) {
      return { invalidChips: true };
    }

    return null;
  };
}
